* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  height: 100vh;
  display: grid;
  place-items: center;
  font-family: sans-serif;
  background-image: linear-gradient(
    to bottom,
    #334c72,
    #2e6f8a,
    #489096,
    #78af9d,
    #b1cba8
  );
  color: rgb(225, 225, 255);
}

form {
  display: flex;
  flex-direction: column;
  padding: 3rem 5rem;
  border-radius: 2em;
  background-color: rgba(60, 62, 84, 0.75);
  width: 30rem;
  border: none;
}

input {
  margin-bottom: 2rem;
  padding: 0.5rem 0.5rem;
  background-color: transparent;
  outline: none;
  border: unset;
  border-bottom: 1.5px solid white;
  font-size: 1rem;
  letter-spacing: 1.5px;
  color: whitesmoke;
  z-index: 1;
}
::placeholder {
  color: whitesmoke;
}
button {
  display: inline-block;
  margin: 1rem 0;
  padding: 0.75rem;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 0.25em;
  font-size: 1rem;
  letter-spacing: 1.5px;
  font-weight: 500;
  transition: all 100ms ease-in-out;
  background-color: rgb(225, 225, 255);
}

button:hover {
  background-color: rgb(235, 235, 235);
}

.success {
  font-size: 3rem;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 10%;
  transform: translateX(-50%);
  text-transform: uppercase;
}
